a{   text-decoration:none !important;}

@media all and (max-width: 480px) {
    .myHeader {
        width: 100%;
        height: 10vh;
        display: grid;
        grid-template-columns: 1fr 3fr 1fr;
        grid-template-rows: 10vh;
        justify-items: center;
        align-items: center;
        box-shadow: 0px 6px 5px #888888;

    }

    .back {
        visibility: hidden;
    }

    .nav {
        width: 45px;
        height: 37.8px;


    }

    .nav:hover li {
        display: block;

    }

    .nav li {
        display: none;
        position: relative;
        width: 20vw;
        height: 5vh;
        right: 20px;
        font-size: 1.3rem;
        color: #FFF;
        z-index: 12;
        box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.2);
    }
    .nav li a{
    text-decoration:none !important;
}

    .nav li:hover {
        width: 18vw;

    }

    #nav-li1 {
        background-color: rgb(230, 0, 18);
    }

    #nav-li2 {
        background-color: rgb(234, 83, 5);
    }

    #nav-li3 {
        background-color: rgb(249, 201, 0);
    }

    #nav-li4 {
        background-color: rgb(0, 172, 56);
    }

    #nav-li5 {
        background-color: rgb(0, 172, 233);
    }

    #nav-li6 {
        background-color: rgb(0, 57, 185);
    }

    #nav-li7 {
        background-color: rgb(137, 12, 132);
    }


}

@media (min-width:480px) and (max-width:858px) {
    .myHeader {
        width: 100%;
        height: 110px;
        box-shadow: 0px 6px 5px #888888;
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        align-items: flex-start;
        justify-content: space-between;
    }

    .back {
        display: none;
    }

    .nav img {
        display: none;
    }

    .nav {
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;

    }

    .nav ul {
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;

    }

    .logo {
        visibility: hidden;
    }

    .nav li {
        height: 100px;
        width: 4rem;
        border-bottom-left-radius: 6px;
        border-bottom-right-radius: 6px;
        box-shadow: 6px 6px 5px #888888;
        color: #FFFFFF;
        font-size: 1.2rem;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    #nav-li1 {
        background-color: rgb(230, 0, 18);
    }

    #nav-li2 {
        background-color: rgb(234, 83, 5);
    }

    #nav-li3 {
        background-color: rgb(249, 201, 0);
    }

    #nav-li4 {
        background-color: rgb(0, 172, 56);
    }

    #nav-li5 {
        background-color: rgb(0, 172, 233);
    }

    #nav-li6 {
        background-color: rgb(0, 57, 185);
    }

    #nav-li7 {
        background-color: rgb(137, 12, 132);
    }

    .nav li:hover {

        height: 90px;
    }

}

