  legend,
  input,
  textarea,
  p,
  blockquote,
  th,
  td {
    margin: 0;
    padding: 0
  }
  
  table {
    border-collapse: collapse;
    border-spacing: 0
  }
  
  fieldset,
  img {
    border: 0
  }
  
  address,
  caption,
  cite,
  code,
  dfn,
