html{color:#000;background:#FFF}body,div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6,pre,code,form,fieldset,legend,input,textarea,p,blockquote,th,td{margin:0;padding:0}table{border-collapse:collapse;border-spacing:0}fieldset,img{border:0}address,caption,cite,code,dfn,em,strong,th,var{font-style:normal;font-weight:normal}ol,ul{list-style:none}caption,th{text-align:left}h1,h2,h3,h4,h5,h6{font-size:100%;font-weight:normal}q:before,q:after{content:''}abbr,acronym{border:0;font-feature-settings:normal;font-variant:normal}sup{vertical-align:text-top}sub{vertical-align:text-bottom}input,textarea,select{font-family:inherit;font-size:inherit;font-weight:inherit;*font-size:100%}legend{color:#000}

.show {
    width:90.6vw;
    margin: 20px auto;
    display: flex;
  
    flex-wrap: wrap;
}

.upload{
    width: 250px;
    height: 280px;
   margin-top: 20px;
}
.photo{
    width: 250px;
    height: 280px;
   margin-top: 20px; 
   border: 3px solid rgb(0, 172, 56);
   overflow: hidden;
   
   border-radius: 3px;
}
.show img{
    width: 250px;
    height: 250px;
}
.upImg{
    background: url(http://localhost/add.svg) no-repeat;
    background-position: center;
    background-size:  70% ;
    display: block-inline;
    border: 3px dashed #000;
    box-sizing:border-box;
    width: 250px;
    height: 250px;
    overflow: hidden;
}
.upImg:hover{
    background-size:  68% ;
}

.photoName{
    width: 100%;
    height: 30px;
    background-color:rgb(0, 172, 56);
    text-align: center;
    box-sizing:border-box;
    border-bottom-right-radius: 1px;
    border-bottom-left-radius: 1px;
    font-weight: bold;
    color: rgb(45,45,45);
}
input[type="button"]{
    display: block;
    width: 100%;
    height: 30px;
    background-color:rgb(92, 94, 93);
    color:#fff;
    border-bottom-right-radius: 1px;
    border-bottom-left-radius: 1px;
    border: 1px solid #000;
    
}

.photoBox{
    width: 250px;
    height: 250px;
 
}

.pbox{
    margin-left: 25px;
    margin-right: 25px;
}
.loading {
    display: flex;
    width: 100vw;
    height: 100vh;
    justify-content: center;
    align-items: center;
}

.piece1 {
    width: 2vw;
    height: 24vh;
    background-color: red;
    margin: 10px;
    -webkit-animation: Loa1 0.5s linear 0s infinite alternate;
            animation: Loa1 0.5s linear 0s infinite alternate;
}

.piece2 {
    width: 2vw;
    height: 20vh;
    background-color: red;
    margin: 10px;
    -webkit-animation: Loa2 0.5s linear 0s infinite alternate;
            animation: Loa2 0.5s linear 0s infinite alternate;

}

.piece3 {
    width: 2vw;
    height: 8vh;
    background-color: red;
    margin: 10px;
    -webkit-animation: Loa3 0.25s linear 0s infinite alternate;
            animation: Loa3 0.25s linear 0s infinite alternate;

}

.piece4 {
    width: 2vw;
    height: 8vh;
    background-color: red;
    margin: 10px;
    -webkit-animation: Loa4 0.5s linear 0s infinite alternate;
            animation: Loa4 0.5s linear 0s infinite alternate;

}

.piece5 {
    width: 2vw;
    height: 8vh;
    background-color: red;
    margin: 10px;
    -webkit-animation: Loa5 0.5s linear 0s infinite alternate;
            animation: Loa5 0.5s linear 0s infinite alternate;

}

@-webkit-keyframes Loa1 {
    0% {
        opacity:1;
        height: 28vh;
    }

    100% {
        opacity:0.2;
        height: 8vh;
    }
}

@keyframes Loa1 {
    0% {
        opacity:1;
        height: 28vh;
    }

    100% {
        opacity:0.2;
        height: 8vh;
    }
}

@-webkit-keyframes Loa2 {
    0% {
        opacity:0.8;
        height: 20vh;
    }

    100% {
        opacity:0.2;
        height: 8vh;
    }
}

@keyframes Loa2 {
    0% {
        opacity:0.8;
        height: 20vh;
    }

    100% {
        opacity:0.2;
        height: 8vh;
    }
}

@-webkit-keyframes Loa3 {
    0% {
        opacity:0.6;
        height: 8vh;
    }

    100% {
        opacity:0.2;
        height: 8vh;
    }
}

@keyframes Loa3 {
    0% {
        opacity:0.6;
        height: 8vh;
    }

    100% {
        opacity:0.2;
        height: 8vh;
    }
}

@-webkit-keyframes Loa4 {
    0% {
        opacity:0.2;
        height: 8vh;
    }

    100% {
        opacity:0.8;
        height: 20vh;
    }
}

@keyframes Loa4 {
    0% {
        opacity:0.2;
        height: 8vh;
    }

    100% {
        opacity:0.8;
        height: 20vh;
    }
}

@-webkit-keyframes Loa5 {
    0% {
        opacity:0.2;
        height: 8vh;
    }

    100% {
        opacity:1;
        height: 28vh;
    }
}

@keyframes Loa5 {
    0% {
        opacity:0.2;
        height: 8vh;
    }

    100% {
        opacity:1;
        height: 28vh;
    }
}

