.bannerImg {
    width: 1200px;
    height: 500px;
    margin: 0 auto;
    background-color: #fff;
    border-top-left-radius:5px;
    border-top-right-radius:5px;
}
.bannerImg img {
    width: 100%;
    height: 100%;

}

.bannerCon {
    width: 1200px;
    height: 24px;
    margin: 0 auto;
    border-left:5px solid rgb(230, 0, 18);
    border-bottom-left-radius:5px;
    border-bottom-right-radius:5px;

    border-right:5px solid rgb(137, 12, 132);
}
.banner ul {
    margin: 0 auto;
    
}
.banner li {
    float: left;
    width: 297.5px;
    height: 24px;
    color: rgb(26, 23, 23);
    font-weight: bold;
    text-align:center;
}
.banner li:hover{
    cursor:pointer
}

.banner {
  margin-top: 10px;
}

@media all and (max-width: 480px) {
  .bannerImg{
    width: 90vw;
    height: 50vh;
  }

  .bannerCon{
    display: none;
  }
}