.loading {
    display: flex;
    width: 100vw;
    height: 100vh;
    justify-content: center;
    align-items: center;
}

.piece1 {
    width: 2vw;
    height: 24vh;
    background-color: red;
    margin: 10px;
    animation: Loa1 0.5s linear 0s infinite alternate;
}

.piece2 {
    width: 2vw;
    height: 20vh;
    background-color: red;
    margin: 10px;
    animation: Loa2 0.5s linear 0s infinite alternate;

}

.piece3 {
    width: 2vw;
    height: 8vh;
    background-color: red;
    margin: 10px;
    animation: Loa3 0.25s linear 0s infinite alternate;

}

.piece4 {
    width: 2vw;
    height: 8vh;
    background-color: red;
    margin: 10px;
    animation: Loa4 0.5s linear 0s infinite alternate;

}

.piece5 {
    width: 2vw;
    height: 8vh;
    background-color: red;
    margin: 10px;
    animation: Loa5 0.5s linear 0s infinite alternate;

}

@keyframes Loa1 {
    0% {
        opacity:1;
        height: 28vh;
    }

    100% {
        opacity:0.2;
        height: 8vh;
    }
}

@keyframes Loa2 {
    0% {
        opacity:0.8;
        height: 20vh;
    }

    100% {
        opacity:0.2;
        height: 8vh;
    }
}

@keyframes Loa3 {
    0% {
        opacity:0.6;
        height: 8vh;
    }

    100% {
        opacity:0.2;
        height: 8vh;
    }
}

@keyframes Loa4 {
    0% {
        opacity:0.2;
        height: 8vh;
    }

    100% {
        opacity:0.8;
        height: 20vh;
    }
}

@keyframes Loa5 {
    0% {
        opacity:0.2;
        height: 8vh;
    }

    100% {
        opacity:1;
        height: 28vh;
    }
}